.message {
    height: 20px;

    p {
        font-size: 12px;
        margin: 0px 0px 10px 10px;
        text-align: start;
        color: red;
    }
}

form {
    background-color: white;
    margin: 0 auto;
    padding: 0px 20px;
    height: 570px;
    width: 400px;
    border: 2px solid grey;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: rgb(60, 60, 60);

    input {
        height: 56px;
        font-size: 16px;
        background-color: rgb(28, 121, 28);
        color: white;
        border: 2px solid transparent;
        border-radius: 5px;
    }

    textarea {
        height: 250px;
        border: 2px solid rgb(213, 209, 209);
        border-radius: 5px;
        padding: 10px;
        font-size: 16px;
    }
    .list{
        height: 400px;
        overflow: auto;
        display: flex;
        flex-direction: column;
        label{
            display: flex;
            align-items: center; 
            height: 40px;
            font-size: 18px;
            input{
                margin-right: 10px;
            }
        }

    }

    .btn {
        height: 40px;
        padding: 5px 25px;
        margin: 0 auto;
        cursor: pointer;
    }

}

textarea:focus {
    outline: none;
}

@media (max-width:465px) {
    form{

        width: 280px;
    }
}